.topFriends{
  display: flex;
  justify-content: space-between;
}
.topFriends li{
  font-size: 10px;
  padding: 5px;
  border: 5px solid; 
  border-image: url('./../../../../public/redframe.png') 300 stretch stretch;
  text-align: center;
}
.friends__img{
  width: 30px;
  height: 30px;
}
.topFriends li img{
  border-radius: 15px;
  margin-bottom: 10px;
}